html, body {
  margin: 0;
  width: 100%;
  background-color: rgb(152, 152, 216);
  font-family: Arial;
}
.App {
  text-align: center;
}

.Cards {
  height: 350px;
  width: 350px;
  background-color: rgba(0, 0, 0, 0.774);
  border: 1px solid red;
  border-radius: 5px;
}

#card-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1500px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

#score {
  position: fixed;
  bottom: 5%;
  right: 5%;
  font-size: 20px;
}

.card-image {
  height: 100%;
  width: 100%;
}

.card-image:hover {
  cursor: pointer;
  background-color: rgba(252, 252, 252, 0.096);
}

@font-face {
  font-family: bloodcrow;
  src: url(./Assets/Fonts/bloodcrow.ttf)
}

h1 {
  font-size: 70px;
  font-family: bloodcrow;
  margin-top: 50px;
}

h2 {
  font-size: 40px;
}

.card-title {
  color: rgb(202, 31, 31);
  font-size: 30px;
  text-align: center;
}

div .winner-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid black;
  background-color: black;
  height: 60px;
  width: 180px;
  user-select: none;
}

.winner-message > p {
  color: red;
  font-size: 20px;
}

@media screen and (max-width: 1515px) {
  .Cards {
    height: 280px;
    width: 280px;
  }
  
  #card-container {
    width: 1200px;
  }

}

@media screen and (max-width: 1215px) {
  .Cards {
    height: 210px;
    width: 210px;
  }
  
  #card-container {
    width: 900px;
  }

  .card-title {
    font-size: 25px;
  }
}

@media screen and (max-width: 915px) {
  .Cards {
    height: 170px;
    width: 170px;
  }
  
  #card-container {
    width: 720px;
  }

  .card-title {
    font-size: 23px;
  }
}

@media screen and (max-width: 730px) {
  .Cards {
    height: 140px;
    width: 140px;
  }
  
  #card-container {
    width: 600px;
  }

  .card-title {
    font-size: 20px;
  }

  h1 {
    font-size: 60px;
  }
  
  h2 {
    font-size: 30px;
  }
}

@media screen and (max-width: 615px) {
  .Cards {
    height: 120px;
    width: 120px;
  }
  
  #card-container {
    width: 520px;
  }

  .card-title {
    font-size: 23px;
  }
}

@media screen and (max-width: 535px) {
  .Cards {
    height: 100px;
    width: 100px;
  }
  
  #card-container {
    width: 450px;
  }

  .card-title {
    font-size: 15px;
  }

  #score {
    font-size: 18px;
  }
}

@media screen and (max-width: 465px) {
  .Cards {
    height: 75px;
    width: 75px;
  }
  
  #card-container {
    width: 340px;
  }

  .card-title {
    font-size: 12px;
  }

  h1 {
    font-size: 50px;
  }
  
  h2 {
    font-size: 25px;
  }

  #score {
    font-size: 17px;
  }
}